import React from "react";
import { ListGroup, Table, Image, Row, Col, Card } from 'react-bootstrap'
import { LineChart, XAxis, YAxis, CartesianGrid, Tooltip, Legend, Line } from 'recharts'

import priceNoso from './images/noso_price.png';
import globe from './images/globe.svg';
import transaction from './images/transaction.svg';
import lastBlock from './images/lastblock.svg';

const Dashboard = ({stats}) => {
    return (
        <Col xs={12} md={12} lg={12}>
            <Card className="std-card-info">
                <Card.Body className="std-card-info-body">
                    <Table striped bordered hover>
                        <Row>
                            <Col xs={12} md={8} lg={8}>
                                <Row>
                                    <Col xs={12} md={6} lg={6}>
                                        <Row>
                                            <Col xs={12} md={12} lg={12}>
                                                <ListGroup variant="flush">
                                                    <ListGroup.Item>
                                                        <div style={{display: 'flex'}}>
                                                            <div style={{paddingLeft: '5px', marginRight: '0.75rem'}}>
                                                                <img src={priceNoso} width="22px" />
                                                            </div>
                                                            <div>
                                                                <div className='dashTitle'>NOSO Price</div>
                                                                <a href='' className='tx-normal'>${stats.nosoPriceUSD} @ {stats.nosoPriceBTC} BTC</a>
                                                            </div>
                                                        </div>
                                                        <hr/>
                                                    </ListGroup.Item>
                                                    <ListGroup.Item>
                                                        <div style={{display: 'flex'}}>
                                                            <div style={{paddingLeft: '5px', marginRight: '0.75rem'}}>
                                                                <img src={globe} width="24px" />
                                                            </div>
                                                            <div>
                                                                <div className='dashTitle'>Market Cap</div>
                                                                <a href='' className='tx-normal'>${(stats.nosoPriceUSD*stats.nosoCirculating).toLocaleString()}</a>
                                                            </div>
                                                        </div>
                                                        <hr className='d-sm-block d-md-none'/>
                                                    </ListGroup.Item>
                                                </ListGroup>
                                            </Col>
                                        </Row>
                                    </Col>
                                    <Col xs={12} md={6} lg={6}>
                                        <Row>
                                            <Col xs={12} md={12} lg={12}>
                                                <ListGroup variant="flush">
                                                    <ListGroup.Item>
                                                        <div style={{display: 'flex'}}>
                                                            <div style={{paddingLeft: '5px', marginRight: '0.75rem'}}>
                                                                <img src={transaction} width="24px" />
                                                            </div>
                                                            <div>
                                                                <div className='dashTitle'>Transactions (last 24hrs)</div>
                                                                <a href='' className='tx-normal'>
                                                                    {stats.nosoTransferred24.reduce((current, next) => current + next[1], 0).toLocaleString()}
                                                                </a>
                                                            </div>
                                                        </div>
                                                        <hr/>
                                                    </ListGroup.Item>
                                                    <ListGroup.Item>
                                                        <div style={{display: 'flex'}}>
                                                            <div style={{paddingLeft: '5px', marginRight: '0.75rem'}}>
                                                                <img src={lastBlock} width="24px" />
                                                            </div>
                                                            <div>
                                                                <div className='dashTitle'>Current Block</div>
                                                                <a href='' className='tx-normal'>{stats.currentBlock.toLocaleString()}</a>&nbsp;
                                                                <span className='tx-normal'>
                                                                    / Next Block in: {Math.floor(stats.timeTilNextBlock/60)}:{(stats.timeTilNextBlock % 60).toLocaleString(undefined, { minimumIntegerDigits: 2})}
                                                                </span>
                                                            </div>
                                                        </div>
                                                    </ListGroup.Item>
                                                </ListGroup>
                                            </Col>
                                        </Row>
                                    </Col>
                                </Row>
                            </Col>
                            <Col xs={12} md={4} lg={4}>
                                <Row>
                                    <Col xs={12} md={12} lg={12} xl={12}>

                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    </Table>
                </Card.Body>
            </Card>
        </Col>
    );
};

export default Dashboard;